.Task__button {
  padding: 5px 15px;
  background-color: #87ceeb;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.Task__button:hover {
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
}

.Task__button:active {
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
  background-color: #4b0082;
  color: #ffffff;
}
