@font-face {
  font-family: SourceSans3;
  src: url("../fonts/SourceSans3.ttf");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: SourceSans3;
}

#root {
  display: flex;
  justify-content: center;
}

.App {
  width: 800px;
  margin-bottom: 150px;
}

.task {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 15px;
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
  margin-top: 15px;
}

.task__content {
  word-break: break-all;
}

.main__title {
  text-align: center;
}

.taskList__placeholder {
  text-align: center;
  margin-top: 100px;
}

.task__buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  min-width: 300px;
}
